// Layout.jsx

// Importowanie komponentu AppBar z folderu components/AppBar
import { AppBar } from 'components/AppBar/AppBar';
// Importowanie funkcji Suspense z React do obsugi leniwej adowania komponentw
import { Suspense } from 'react';
// Importowanie komponentu Outlet z react-router-dom do renderowania zagniedonych tras
import { Outlet } from 'react-router-dom';

export const Layout = () => {
  return (
    <>
      {/* Komentarz: Przestrze przed AppBar, aby nie zakrywa treci */}
      <AppBar />
      {/* Komentarz: Suspense do obsugi leniwej adowania */}
      <Suspense fallback={null}>
        <Outlet /> {/* Komentarz: Outlet renderuje zagniedone trasy */}
      </Suspense>
    </>
  );
};
