// App.jsx

// Importowanie komponentu Spiner ze stylw ContactList
import { Spiner } from 'pages/ContactList/ContactList.styled';
// Importowanie funkcji lazy i hooka useEffect z react
import { lazy, useEffect } from 'react';
// Importowanie hookw useDispatch i useSelector z react-redux
import { useDispatch, useSelector } from 'react-redux';
// Importowanie komponentw Navigate, Route i Routes z react-router-dom
import { Navigate, Route, Routes } from 'react-router-dom';
// Importowanie komponentu ToastContainer z react-toastify do obsugi powiadomie
import { ToastContainer } from 'react-toastify';
// Importowanie akcji refreshUser z operacji autoryzacji do odwieania tokena
import { refreshUser } from '../Redux/Authorization/operations';
// Importowanie globalnych stylw
import { GlobalStyle } from './GlobalStyle';
// Importowanie komponentu Layout
import { Layout } from './Layout/Layout';
// Importowanie komponentu PrivateRoute do ochrony prywatnych tras
import { PrivateRoute } from './PrivateRoute';
// Importowanie komponentu RestrictedRoute do ochrony tras z ograniczeniami
import { RestrictedRoute } from './RestrictedRoute';

// Lazy loading dla stron rejestracji, gwnej, logowania i listy kontaktw
const Register = lazy(() => import('../pages/Register/Register'));
const Home = lazy(() => import('../pages/Home/Home'));
const Login = lazy(() => import('../pages/Login/Login'));
const Contactlist = lazy(() => import('../pages/ContactList/ContactList'));

export const App = () => {
  // Inicjalizacja hooka useDispatch
  const dispatch = useDispatch();

  // Hook useEffect do odwieania tokena przy zaadowaniu komponentu
  useEffect(() => {
    dispatch(refreshUser());
  }, [dispatch]);

  // Pobieranie stanu isRefreshing ze stanu Redux
  const { isRefreshing } = useSelector(state => state.auth);

  // Jeli token nie jest odwieany, renderowanie komponentw
  return !isRefreshing ? (
    <>
      {/* Definiowanie tras */}
      <Routes>
        <Route path="/" element={<Layout />}>
          {/* Gwna strona */}
          <Route index element={<Home />} />

          {/* Prywatna trasa dla listy kontaktw */}
          <Route
            path="/contacts"
            element={
              <PrivateRoute component={<Contactlist />} redirectTo="/login" />
            }
          />

          {/* Ograniczona trasa dla logowania */}
          <Route
            path="/login"
            element={
              <RestrictedRoute redirectTo="/contacts" component={<Login />} />
            }
          />

          {/* Ograniczona trasa dla rejestracji */}
          <Route
            path="/register"
            element={
              <RestrictedRoute
                redirectTo="/contacts"
                component={<Register />}
              />
            }
          />

          {/* Przekierowanie dla nieznanych tras */}
          <Route path="*" element={<Navigate to="/" />} />
        </Route>
      </Routes>

      {/* Kontener dla powiadomie */}
      <ToastContainer />

      {/* Globalne style */}
      <GlobalStyle />
    </>
  ) : (
    // Wywietlanie spinnera podczas odwieania tokena
    <Spiner />
  );
};
