// UseMenu.jsx

// Importowanie ikony wylogowania z ant-design icons
import { LogoutOutlined } from '@ant-design/icons';
// Importowanie komponentw Avatar i Button z ant-design
import { Avatar, Button } from 'antd';
// Importowanie hookw useDispatch i useSelector z react-redux
import { useDispatch, useSelector } from 'react-redux';
// Importowanie akcji logOut z operacji autoryzacji
import { logOut } from '../../Redux/Authorization/operations';
// Importowanie stylw z UserMenu.styled
import { Wrap } from './Usermenu.styled';

export const UserMenu = () => {
  // Wycigamy email uytkownika ze stanu Redux
  const { email } = useSelector(state => state.auth.user);

  // Uywamy dispatch do wysyania akcji Redux
  const dispatch = useDispatch();

  return (
    <Wrap>
      {/* Wywietlamy awatar z pierwsz liter email */}
      <Avatar>{email.slice(0, 1).toUpperCase()}</Avatar>
      {/* Wywietlamy email uytkownika */}
      <p>{email}</p>
      {/* Przycisk wylogowania z ikon */}
      <Button type="primary" onClick={() => dispatch(logOut())}>
        <LogoutOutlined /> Log out
      </Button>
      {/* Pusty element {' '} dla odstpu */}
    </Wrap>
  );
};
